<template>
    <div class="drop-footer">
        <div class="footer-drop-group">

            <!-- 遍历出选项 -->
            <a class="footer-drop-btn" v-for="v in list" :key="'item_' + v" @click="clickItem(v)">
                {{ v }}
            </a>

        </div>
    </div>
</template>

<script>
export default {
    name: "DropFooter",
    props: {
        items: Array
    },
    data () {
        return {
            list: [
                '选项1',
                '选项2',
                '选项3'
            ]
        }
    },
    created () {
        if (this.items) this.list = this.items
    },
    methods: {
        //点击选项
        clickItem (item) {
            this.$emit('change', item)
        }
    }
};
</script>

